@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap);
.page-login {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
}

.backgroud-image {
  height: 100%;
  width: 60%;
  background-image: url(/trybe-wallet/static/media/background.671c45ac.jpg);
  background-position: center;
  background-size: cover;
}

.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
}

.login {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 80%;
}

.login h1 {
  font-size: 4em;
  font-weight: 700;
  color: var(--main-color);
  margin-bottom: 25px;
}

.login input {
  width: 80%;
  height: 5vh;
  padding: 5px;
  font-size: 1.4em;
  margin-bottom: 10px;
  border-radius: 3px;
}

.login a {
  width: 80%;
}

.login button:disabled {
  cursor: auto;
  background-color: #49e6ac;
}

.login button {
  border: none;
  border-radius: 3px;
  cursor: pointer;
  height: 5vh;
  width: 100%;
  font-size: 1.2em;
  color: white;
  background-color: var(--main-color);
}

header {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  padding: 0 25px;
  align-items: center;
  background-color: var(--main-white);
}

.logo-header {
  color: var(--main-color);
}

.information-header {
  width: 30%;
  display: flex;
}

.information-header p {
  width: 50%;
}

.form {
  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  background-color: #001219;
}

.valor-input {
  width: 5vw;
  height: 4vh;
  margin-left: 5px;
  margin-right: 15px;
  padding: 5px;
  font-size: 1.2em;
  text-align: center;
  border-radius: 3px;
  border: none;
}

.descricao-input {
  width: 12vw;
  height: 4vh;
  margin-left: 5px;
  margin-right: 15px;
  padding: 5px;
  font-size: 1.2em;
  border-radius: 3px;
  border: none;
}

.form select {
  margin-left: 5px;
  margin-right: 15px;
  border-radius: 3px;
  padding: 5px 10px;
  font-size: 1.2em;
}

.form button {
  cursor: pointer;
  color: white;
  border: none;
  border-radius: 6px;
  background-color: #2e50eb;
  padding: 10px 15px;
}

.form button:hover {
  background-color: #1b40e9;
}

table {
  width: 100vw;
  border-collapse: collapse;
}

th {
  height: 50px;
}

td {
  border: 2px solid #5c616b;
  text-align: center;
  background-color: var(--main-white);
}

.white-table {
  background-color: #b4b4b4;
}

tr, th {
  border: 2px solid #5c616b;
}

thead {
  height: 25px;
  color: white;
  background-color: #293241;
}

.table-bot {
  width: 40px;
  height: 30px;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  text-align: center;
  margin: 5px;
  border: none;
  border-radius: 6px;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.edit {
  background-color: #ffb703;
}

.delete {
  background-color: #e63946;
}

:root {
  --main-color: #2fc18c;
  --main-white: #e5e5e5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

body, #root {
  width: 100vw;
}
